import React from 'react';
import { Card, CardHeader, CardText } from 'material-ui/Card'

const Disclaimer = () => (
  <Card style={{marginTop: '2rem'}}
  >
    <CardHeader
      title='Disclaimer'
      actAsExpander={true}
      showExpandableButton={true}
    />
    <CardText expandable={true}>
      <div>
        <p>Este  um projeto em construo. Os valores calculados so apenas
        uma referncia e no refletem valores reais.</p>
        <p>Para valores mais precisos, consulte a legislao vigente ou
        um contador.</p>
        <h5>O clculo de CLT considera:</h5>
        <ul>
          <li>Descontos de INSS e IRRF</li>
          <li>11 salrios normais, 1 salrio com adicional de 1/3 de frias
          e 13 salrio</li>
          <li>O resultado so valores lquidos recebidos. Para tomar decises
             importante considerar benefcios e outros descontos</li>
        </ul>
        <h5>O clculo de PJ considera:</h5>
        <ul>
          <li>Pessoa jurdica do tipo MEI</li>
          <li>12 recebimentos ao ano</li>
          <li>Desconto mensal da DAS</li>
          <li>Esta forma de contratao no  estritamente legal mas  uma
          prtica de mercado.</li>
        </ul>
        <h5>Features sendo desenvolvidas:</h5>
        <ul>
          <li>Adicionar outros benefcios e descontos  CLT, como FGTS, VT, VR
          </li>
          <li>Adicionar  PJ custos de alimentao e transporte</li>
          <li>Adicionar opes de PJ LTDA com suas formas de tributao</li>
          <li>Exibir os clculos, para mostrar como  difcil chegar nesses
            nmeros</li>
        </ul>
      </div>
    </CardText>
  </Card>
);

export default Disclaimer



